<template>
	<view class="chart-container">
		<view v-if="item.headerTextResult" class="parser">
			<parser :html="item.headerTextResult.text" ref="header" />
		</view>
		<view v-if="item.chartResult" class="chart-cavans-container">
			<!--#ifndef MP-ALIPAY -->
			<canvas :canvas-id="canvasId" id="canvasId" class="chart-canvas" style="width: 750upx; height: 500upx;"
				@touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd" />
			<!--#endif-->
		</view>
		<view v-if="item.footerTextResult" class="parser">
			<parser :html="item.footerTextResult" ref="footer" />
		</view>
	</view>
</template>

<script>
	import parser from "@/components/parser";
	import uCharts from '@/packageSubPage/components/u-charts/u-charts';

	const canvasMap = {};
	
	let canvasIdSeed = 0;

	var _self;

	export default {
		name: 'surveyResultElement',
		components: { parser },
		data() {
			return {
				id: null,
				cWidth: '',
				cHeight: '',
				pixelRatio: 1,
				canvasId: `canvasId_${canvasIdSeed++}`
			};
		},
		props: {
			item: {
				type: Object,
				default() {
					return {
						headerTextResult: null,
						footerTextResult: null,
						chartResult: null
					};
				}
			},
		},
		mounted() {
			_self = this;
			
			this.loadCharts();
		},
		watch: {
			item() {
				this.loadCharts();
			}
		},
		updated() {
			// this.loadCharts();
		},
		methods: {
			loadCharts() {
				if(!this.item || !this.item.chartResult || !this.canvasId) return;
				const canvasId = this.canvasId;
				canvasMap[canvasId] = new uCharts({
					...this.item.chartResult,
					$this: _self,
					canvasId
				});
			},
			touchStart(e){
				const canvas = canvasMap[this.canvasId];
				canvas.scrollStart(e);
				this.$emit('touchStart', e, canvas);
			},
			touchMove(e) {
				const canvas = canvasMap[this.canvasId];
				canvas.scroll(e);
				this.$emit('touchMove', e, canvas);
			},
			touchEnd(e) {
				const canvas = canvasMap[this.canvasId];
				canvas.scrollEnd(e);
				this.$emit('touchEnd', e, canvas);
			},
			toJSON() {},
		}
	}
</script>

